import {
  Form,
  ImageUploader,
  Input,
  NavBar,
  TextArea,
  Dialog,
} from 'antd-mobile';
import PouchDB from 'pouchdb';
import React from 'react';
import { withRouter } from 'react-router-dom';
import { Loading, UploadButton } from '../../components';
import { dbName, getOne } from '../../KeyPoints';

class TaskEdit extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      fileList: [],
      loading: false,
    };

    this.formRef = React.createRef();
    this.dbRef = React.createRef();
    this.dbRef.current = new PouchDB(dbName);

    this.back = this.back.bind(this);
    this.uploadFile = this.uploadFile.bind(this);
    this.changeFileList = this.changeFileList.bind(this);
  }

  componentDidMount() {
    const { _id } = this.props.match.params;
    this.refresh(_id);
  }

  componentWillUnmount() {
    this.dbRef.current.close();
  }

  back() {
    this.props.history.go(-1);
  }

  /**
   * 
   */
  refresh(_id) {
    getOne(this.dbRef.current, _id)
      .then((doc) => {
        this.formRef.current?.setFieldsValue(doc);
      })
      .catch((err) => {});
  }

  changeFileList(fileList) {
    this.setState({ fileList });
  }

  uploadFile(file) {
    console.log('uplaod file', file);
// lastModified: 1632085881000
// lastModifiedDate: Mon Sep 20 2021 05:11:21 GMT+0800 () {}
// name: "1.png"
// size: 91861
// type: "image/png"
// webkitRelativePath: ""

    return { url: URL.createObjectURL(file) };
  }

  render() {
    const { loading, fileList } = this.state;

    return (
      <>
        <NavBar className="nav-bar" onBack={this.back}>
          
        </NavBar>

        <Form
          ref={this.formRef}
          layout="horizontal"
          style={{ marginTop: '45px' }}
        >
          <Form.Item name="station_name" label="">
            <Input placeholder="" />
          </Form.Item>

          <Form.Item name="key_point_type" label="">
            <Input placeholder="" />
          </Form.Item>

          <Form.Item name="name" label="">
            <Input placeholder="" />
          </Form.Item>

          <Form.Item name="type" label="">
            <Input placeholder="" />
          </Form.Item>

          <Form.Item name="track_index" label="">
            <Input type="number" placeholder="" />
          </Form.Item>

          <Form.Item name="position" label="">
            <Input type="number" placeholder="()" />
          </Form.Item>

          <Form.Item name="mile" label="">
            <Input placeholder="" />
          </Form.Item>

          <Form.Item name="remark" label="">
            <TextArea
              autoSize={{ minRows: 3, maxRows: 5 }}
              placeholder=""
            />
          </Form.Item>

          <Form.Item name="images" label="">
            <ImageUploader
              multiple
              value={fileList}
              onChange={this.changeFileList}
              upload={this.uploadFile}
              beforeUploadFile={this.beforeUploadFile}
              onDelete={(file) => {
                return Dialog.confirm({
                  content: '',
                  onConfirm: () => {},
                });
              }}
            />
          </Form.Item>
        </Form>

        <Loading visible={loading} />
      </>
    );
  }
}

export default withRouter(TaskEdit);
